@import url(https://fonts.googleapis.com/css2?family=Trispace:wght@200;500&display=swap);
:root {
    --brown: #493A1C;
    --coral: #FF806C;
    --teal: rgb(48, 162, 228);
    --lightblue: #BDF6E9;
    --green: #78C29E;
  }
.wrapper {
    width: 100%;
    height: 100%;
    padding-top: 40px;
    background: linear-gradient(15deg, rgb(255, 255, 255) 30%, rgb(48, 162, 228) 150%);
}
.cardHeader {
    background-color: #FF806C;
    background-color: var(--coral);
    font-family: 'Trispace', sans-serif;
    box-shadow: rgb(48, 162, 228) 0px -2px 15px;
    box-shadow: var(--teal) 0px -2px 15px;
}
.footer {
    margin-top: 40px;
    border-top: 1px solid #493A1C;
    border-top: 1px solid var(--brown);
    padding: 30px;

}
.googleFont {
    font-family: 'Trispace', sans-serif !important;
    font-weight: 500 !important;
    color: #493A1C;
    color: var(--brown);
}
.googleFont2 {
    font-family: 'Trispace', sans-serif !important;
    font-weight: 200 !important;
    color: #493A1C;
    color: var(--brown);
    opacity: 60%;
}
.card {
    box-shadow: -3px 5px 10px rgb(48, 162, 228) !important;
    box-shadow: -3px 5px 10px var(--teal) !important;
    
}
.homelink {
    text-decoration: none;
    color: #493A1C;
    font-size: 1.2em;
}
.aboutGrid {
    padding: 40px 25px 0px 25px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.heroContent {
  display: none;
}
